
<template>
  <div class="check_tab_content">
    <div class="tab_class">
      <el-table
        ref="multipleTable"
        tooltip-effect="dark"
        style="width: 100%"
        border
        stripe
        :data="tableDataComputed"
        :class="tabClass"
        @selection-change="handleSelectionChange"
      >
        <!-- <el-table-column type="selection" width="50"></el-table-column> -->
        <el-table-column label="序号" width="50" type="index" sortable></el-table-column>
        <el-table-column prop="enclosure" label="附件" width="100" sortable></el-table-column>
        <el-table-column prop="mailAccount" label="邮件账户" min-width="120" sortable></el-table-column>
        <el-table-column prop="mailLocation" label="邮件位置" min-width="120" sortable></el-table-column>
        <el-table-column prop="theme" label="主题" sortable></el-table-column>
        <el-table-column prop="addressee" label="收件人" min-width="100" sortable></el-table-column>
        <el-table-column prop="from" label="发件人" min-width="100" sortable></el-table-column>
        <el-table-column prop="date" label="日期" sortable></el-table-column>
        <el-table-column prop="size" label="大小" sortable></el-table-column>
      </el-table>
    </div>

    <!-- 下拉框 -->
    <div class="select_class">
      <el-button>邮件检查报表</el-button>
    </div>
    <div style="height:6%;border:1px solid #ddd;margin-bottom: 8px;"></div>
    <div style="height:23%;border:1px solid #ddd"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tabClass: "small_tab_class",
      tableData: [
        // {
        //   enclosure: "", //附件
        //   mailAccount: "", //邮件账户
        //   mailLocation: "", //邮件位置
        //   theme: "", //主题
        //   addressee: "", //收件人
        //   from: "", //发件人
        //   date: "", //日期
        //   size: "" //大小
        // }
      ],
      multipleSelection: [],
      pagesize: 5, //每页数量
      currentPage: 1, //初始页
      options: [
        {
          value: "选项1",
          label: "绝密"
        },
        {
          value: "选项2",
          label: "机密"
        },
        {
          value: "选项3",
          label: "秘密"
        },
        {
          value: "选项4",
          label: "内部"
        },
        {
          value: "选项5",
          label: "公开"
        },
        {
          value: "选项6",
          label: "待鉴定"
        }
      ],
      value1: "",
      options2: [
        {
          value: "选项1",
          label: "文件夹"
        },
        {
          value: "选项2",
          label: "文件"
        }
      ],
      value2: "",
      options3: [
        {
          value: "选项1",
          label: "保存判定信息"
        },
        {
          value: "选项2",
          label: "清空判定信息"
        }
      ],
      value3: ""
    };
  },
  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      this.pagesize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    }
  },
  computed: {}
};
</script>

<style lang="less" scoped>
/deep/.cell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/deep/.el-table td,
/deep/.el-table th {
  text-align: left;
  padding: 0;
  height: 30px;
  line-height: 30px;
  color: #606266;
}
/deep/.el-table__empty-block {
  min-height: 260px;
}

.small_tab_class {
  height: 100%;
}
.big_tab_class {
  height: 500px;
}
/deep/.el-tabs {
  height: 450px;
  border: 1px solid #ddd;
}

// 分页
.paging {
  text-align: right;
  margin: 8px 0;
  padding-right: 8px;
  border: 1px solid #eee;
}
.check_tab_content {
  height: 100%;
  .tab_class {
    height: 62%;
  }
}

// 下拉框
.select_class {
  text-align: right;
  padding: 5px 8px 5px 0;
  border: 1px solid #eee;
  margin: 8px 0;

  /deep/.el-input {
    width: 140px !important;
    margin-left: 4px;
    height: 30px;
  }
  /deep/.el-input__inner {
    width: 140px !important;
    margin-left: 4px;
    height: 30px;
  }
  /deep/.el-input--suffix .el-input__inner {
    width: 140px !important;
    margin-left: 4px;
    height: 30px;
  }
  /deep/.el-input__icon {
    height: 116%;
  }
  /deep/.el-button {
    height: 30px;
    padding-top: 8px;
    margin-left: 7px;
  }
}
</style>
